<template>
  <div class="vux-uploader">
    <div class="vux-uploader_hd">
      <p class="vux-uploader_title">{{ title }}</p>
      <div class="vux-uploader_info">{{ fileList.length }} / {{ max }}</div>
    </div>
    <div class="vux-uploader_bd">
      <ul class="vux-uploader_files">
        <li class="vux-uploader_file" v-for="(item, index) in fileList" :key="index" :style="{
          backgroundImage: `url(${item})`
        }" @click="handleFileClick(item, index)">
        </li>
      </ul>
      <div class="vux-uploader_input-box" v-show="files.length < max">
        <input class="vux-uploader_input" ref="input" type="file" accept="image/*" :capture="capture" @change="change"/>
      </div>
    </div>
    <div class="vux-uploader_previewer" id="previewer" @click="hidePreviewer">
      <div class="vux-uploader_preview-img" id="previewerImg"></div>
      <div class="vux-uploader_del" @click="deleteImg"></div>
    </div>
  </div>
</template>
<script>
import EXIF from "exif-js";
import {
  detectVerticalSquash,
  detectSubsampling,
  transformCoordinate
} from "../utils";

// compatibility for window.URL
const URL =
  window.URL && window.URL.createObjectURL
    ? window.URL
    : window.webkitURL && window.webkitURL.createObjectURL
      ? window.webkitURL
      : null;

export default {
  name: "Uploader",
  props: {
    title: {
      type: String,
      default: "图片上传"
    },
    files: {
      type: Array,
      default: []
    },
    max: {
      type: Number | String,
      default: 5
    },
    capture: {
      type: Boolean | String,
      default: false,
    },
    compress: {
      type: Boolean,
      default: true
    },
    maxWidth: {
      type: String | Number,
      default: 500
    }
  },
  data() {
    return {
      fileList: [],
      currentIndex: 0
    };
  },
  mounted() {
    this.fileList = this.fileList.concat(this.files);
    this.hidePreviewer();
  },
  methods: {
    change(e) {
      const target = e.target || e.srcElement;
      const file = target.files[0];
      this.readFile(file);
    },
    readFile(file) {
      const { renderImageToDataUrl, fileList } = this;
      if (window.Blob && file instanceof Blob) {
        if (!URL) {
          throw Error("No createObjectURL function found to create blob url");
        }
        const image = new Image();
        image.src = URL.createObjectURL(file);
        const doSquash = !file || file.type === "image/jpeg";
        image.onload = () => {
          // 在调用EXIF的getData或者其他方法时，必须等image完全加载完   https://github.com/exif-js/exif-js#usage
          EXIF.getData(image, function() {
            const orientation = EXIF.getTag(this, "Orientation");
            console.log("EXIF orientation: ", orientation);

            const dataUrl = renderImageToDataUrl(image, orientation, doSquash);
            fileList.push(dataUrl);
            URL.revokeObjectURL(image.src);
          });
        };
      }
    },
    renderImageToDataUrl(image, orientation, doSquash) {

      const canvas = document.createElement("canvas");
      const ctx = canvas.getContext("2d");

      let w = image.naturalWidth;
      let h = image.naturalHeight;
      // console.log("Image origin width & height:", w, h);

      const subsampled = detectSubsampling(image);
      if (subsampled) {
        w /= 2;
        h /= 2;
      }

      const vertSquashRatio = detectVerticalSquash(image);
      // console.log("Vertical Squash Ratio: ", vertSquashRatio);

      // 屏幕的设备像素比
      const ratio = window.devicePixelRatio || 1;
      // console.log('Device Ratio: ', ratio);

      const dw = Math.min(this.maxWidth, w) * ratio;
      const dh = h * (dw / w) / vertSquashRatio;

      transformCoordinate(canvas, ctx, dw, dh, orientation);

      ctx.clearRect(0, 0, dw, dh);
      ctx.drawImage(image, 0, 0, dw, dh);

      const dataUrl = canvas.toDataURL("image/jpeg");
      const rate = dw / w * 100;
      // console.log("Compress Ratio: ", rate.toFixed(2) + "%");
      return dataUrl;
    },
    handleFileClick(item, index) {
      this.showPreviewer();
      const previewerImg = document.getElementById("previewerImg");
      previewerImg.style.backgroundImage = `url(${item})`;
      this.currentIndex = index;
    },
    showPreviewer() {
      const previewer = document.getElementById("previewer");
      if (previewer) {
        previewer.style.display = "block";
        previewer.style.opacity = 1;
      }
    },
    hidePreviewer() {
      const previewer = document.getElementById("previewer");
      if (previewer) {
        previewer.style.display = "none";
        previewer.style.opacity = 0;
      }
    },
    deleteImg() {
      const { currentIndex, fileList } = this;
      this.hidePreviewer();
      fileList.splice(currentIndex, 1);
    }
  }
};
</script>
<style lang="less">
@font-face {
  font-weight: normal;
  font-style: normal;
  font-family: "weui";
  src: url("data:application/octet-stream;base64,")
    format("truetype");
}

.vux-uploader {
  padding: 10px 15px;
  .vux-uploader_hd {
    display: flex;
    padding-bottom: 10px;
    .vux-uploader_title {
      flex: 1;
    }
    .vux-uploader_info {
      color: #b2b2b2;
    }
  }
  .vux-uploader_bd {
    overflow: hidden;
    .vux-uploader_files {
      list-style: none;
      .vux-uploader_file {
        float: left;
        margin-right: 9px;
        margin-bottom: 9px;
        width: 79px;
        height: 79px;
        background: no-repeat center center;
        background-size: cover;
      }
    }
    .vux-uploader_input-box {
      float: left;
      position: relative;
      margin-right: 9px;
      margin-bottom: 9px;
      width: 77px;
      height: 77px;
      border: 1px solid #d9d9d9;
      &:before,
      &:after {
        content: " ";
        position: absolute;
        top: 50%;
        left: 50%;
        -webkit-transform: translate(-50%, -50%);
        transform: translate(-50%, -50%);
        background-color: #d9d9d9;
      }
      &:before {
        width: 2px;
        height: 39.5px;
      }
      &:after {
        width: 39.5px;
        height: 2px;
      }
      .vux-uploader_input {
        position: absolute;
        z-index: 1;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        opacity: 0;
        -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
      }
    }
  }
  .vux-uploader_previewer {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background: #000;
    z-index: 1000;
    .vux-uploader_preview-img {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 60px;
      left: 0;
      background: center center no-repeat;
      background-size: contain;
    }
    .vux-uploader_del {
      position: absolute;
      right: 0;
      bottom: 0;
      left: 0;
      background-color: #0d0d0d;
      color: #ffffff;
      height: 60px;
      line-height: 60px;
      text-align: center;
      font-family: "weui";
      &:after {
        color: #ffffff;
        font-size: 22px;
        content: "\EA11";
      }
    }
  }
}
</style>



